body {
  background-color: #000;
  color: #FFF;
  font-family: roboto, sans-serif;
}

.header {
  position: fixed;
  z-index: 11;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  -webkit-transition: all cubic-bezier(0, 1.01, 0.12, 1.01) 1s;
  transition: all cubic-bezier(0, 1.01, 0.12, 1.01) 1s;
}

.header.up .logos,.header.up .logo,.header.up .menu {
  -webkit-transform: translate3d(0, -40px, 0) !important;
  transform: translate3d(0, -40px, 0) !important;
  opacity: 0 !important;
  -webkit-transition: all cubic-bezier(0, 1.01, 0.12, 1.01) 1s;
  transition: all cubic-bezier(0, 1.01, 0.12, 1.01) 1s;
}

.header .logos {
  z-index: 99;
  position: fixed;
  top: 80px;
  left: 6vw;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-transition: all cubic-bezier(0, 1.01, 0.12, 1.01) 2s;
  transition: all cubic-bezier(0, 1.01, 0.12, 1.01) 2s;
}

.header.down {
  background-color: #000;
  background-color: #000;
}

.header.down .logos,.header.down .logo,.header.down .menu {
  transform: translate3d(0, -40px, 0) !important;
}

.header .logos a {
  max-width: 120px;
  -webkit-transition: all cubic-bezier(0, 1.01, 0.12, 1.01) 2s;
  transition: all cubic-bezier(0, 1.01, 0.12, 1.01) 2s;
}

.header .logos a:not(:first-child) {
  display: none;
}

.header .logos a.marca {
  max-width: 85px;
  min-width: 80px;
  filter: invert(1);
}

.header .logos a.expansion {
  max-width: 96px;
  min-width: 86px;
}

.header .logos a.telva {
  max-width: 90px;
  min-width: 80px;
}

.header .logos a img {
  min-width: 80px;
}

.header .logos a:first-child {
  margin-right: 25px;
  margin-bottom: 4px;
}

.header .logos a img {
  display: block;
  width: 100%;
}

.header .logo {
  z-index: 99;
  position: fixed;
  top: 64px;
  left: 0;
  right: 0;
  margin: auto;
  width: 160px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-transition: all cubic-bezier(0, 1.01, 0.12, 1.01) 2s;
  transition: all cubic-bezier(0, 1.01, 0.12, 1.01) 2s;
}

.header .logo a {
  display: block;
  font-family: ivymode, sans-serif;
  text-align: center;
  text-decoration: none;
  color: #000;
  font-size: calc(24px + .2vw);
  line-height: 130%;
  font-weight: 300;
  letter-spacing: 1px;
  -webkit-transition: all cubic-bezier(0, 1.01, 0.12, 1.01) 2s;
  transition: all cubic-bezier(0, 1.01, 0.12, 1.01) 2s;
}

.header .logo a span {
  display: block;
  font-weight: 300;
  font-style: italic;
}

.header .menu {
  z-index: 99;
  position: fixed;
  top: 80px;
  right: 6vw;
  width: 118px;
  border-radius: 50%;
  -webkit-transition: all cubic-bezier(0, 1.01, 0.12, 1.01) 2s;
  transition: all cubic-bezier(0, 1.01, 0.12, 1.01) 2s;
}

.header .menu:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
  background-color: #efedeb;
  -webkit-transform: scale3d(0.6, 0.6, 1);
  transform: scale3d(0.6, 0.6, 1);
  opacity: 0;
  border-radius: 50%;
  -webkit-transition: all cubic-bezier(0, 1.01, 0.12, 1.01) 3s;
  transition: all cubic-bezier(0, 1.01, 0.12, 1.01) 3s;
}

.header .menu:hover:before {
  -webkit-transform: scale3d(0.99, 0.99, 1);
  transform: scale3d(0.99, 0.99, 1);
  opacity: 1;
}

.header .menu.active span:first-child {
  opacity: 0;
  -webkit-transform: translate3d(0, 20px, 0);
  transform: translate3d(0, 20px, 0);
  pointer-events: none;
}

.header .menu.active span:last-child {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  pointer-events: auto;
}

.header .menu span {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  font-weight: 600;
  -webkit-transition: all cubic-bezier(0, 1.01, 0.12, 1.01) 2s;
  transition: all cubic-bezier(0, 1.01, 0.12, 1.01) 2s;
}

.header .menu span:last-child {
  opacity: 0;
  -webkit-transform: translate3d(0, 20px, 0);
  transform: translate3d(0, 20px, 0);
  pointer-events: none;
}

.article {
  padding-top: calc(100px + 8vw);
  margin-bottom: calc(20px + 5vw);
}

.article .article-wrapper {
  display: flex;
}

.article .article-wrapper .article-video {
  width: 50%;
  max-width: 50%;
  flex: 0 0 50%;
  position: sticky;
  top: 5%;
  height: 20vh;
}

.article .article-wrapper .article-content {
  position: relative;
  width: 50%;
  max-width: 50%;
  flex: 0 0 50%;
}

.article .article-wrapper .article-content .article-bg {
  position: absolute;
  z-index: -1;
  pointer-events: none;
  top: 0;
  left: 0;
  width: 100%;
}

.article .article-wrapper .article-content .article-bg img {
  width: 50%;
  position: absolute;
  right: 0;
}

.article .article-wrapper .article-content .ante {
  font-size: 18px;
  color: #ee1e52;
  margin-bottom: 10px;
}

.article .article-wrapper .article-content h2 {
  font-size: calc(22px + 2vw);
  font-weight: 600;
  line-height: 1.1;
  max-width: 800px;
  margin-bottom: 60px;
}

.article .article-wrapper .article-content p {
  max-width: 600px;
  font-size: 18px;
  margin-bottom: 40px;
  line-height: 160%;
  font-weight: 400;
}

.article .article-wrapper .article-content .featured {
  background-color: #1b1b1b;
  margin: calc(20px + 3vw) 0;
}

.article .article-wrapper .article-content .featured p {
  padding: 0 15px;
  margin: 0 auto;
  max-width: 400px;
  font-size: 18px;
  line-height: 160%;
}

.footer {
  width: 100%;
  margin: 0 auto;
  padding: calc(30px + 3vw) 0;
  margin-top: calc(20px + 20vw);
  position: relative;
  z-index: 100;
  color: #FFF;
}

.footer .footer-content {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
}

.footer .footer-content > a {
  display: block;
  margin: 0 auto;
  margin-bottom: 30px;
  max-width: 140px;
  min-width: 120px;
  text-decoration: underline;
}

.footer .footer-content p {
  margin-bottom: 20px;
  font-size: 14px;
  font-weight: 500;
  line-height: 140%;
  opacity: 1;
  letter-spacing: .5px;
}

.footer .footer-content a {
  color: #FFF;
  text-decoration: underline;
}

.footer .footer-credits {
  text-transform: uppercase;
  opacity: .4;
  font-weight: 200;
  margin-top: 30px;
  display: flex;
  justify-content: center;
}

.footer .footer-credits li {
  font-weight: 200;
  margin: 0 10px;
  font-size: 14px;
  letter-spacing: 1px;
}

.footer .footer-credits li span {
  display: block;
  font-weight: 200;
  font-size: 14px;
}

.share {
  display: block;
  margin: 20px 0;
}

.share > span {
  display: block;
  font-size: 12px;
  color: #FFF;
  font-weight: 600;
  margin-bottom: 10px;
}

.share ul {
  display: flex;
}

.share ul li a {
  margin: 0 5px;
  border-radius: 50%;
  width: 70px;
  height: 70px;
  border: 1px solid rgba(255, 255, 255, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.share ul li a img {
  width: 20px;
  filter: grayscale(1);
  filter: invert(1);
}

