.row {
    position: relative;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
    justify-content: space-between;
    -ms-flex-pack: justify;
    align-items: center;
    -ms-flex-align: center;
    flex-flow: row wrap;
    &.right {
        -webkit-justify-content: flex-end;
        justify-content: flex-end;
        -ms-flex-pack: end;
    }
}

.content-md {
    max-width: 1010px;
    margin: 0 auto;
}

.content-lg {
    max-width: 1180px;
    margin: 0 auto;
}

.right {
    margin-left:auto;
}

.center {
    margin:0 auto;
    text-align: center;
}

